/* Masonry Styles */

#masonry {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  font-size: 0;
  justify-content: flex-start;
}
#masonry img {
  /* width: 33.3%; */
  transition: 0.8s opacity;
  cursor: zoom-in;
}

#masonry:hover img {
  opacity: 0.4;
}
#masonry:hover img:hover {
  opacity: 1;
}

/* fallback for earlier versions of Firefox */

@supports not (flex-wrap: wrap) {
  #masonry {
    display: block;
  }
  #masonry img {
    display: inline-block;
    vertical-align: top;
  }
}
